import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import image1 from '../images/img1.jpg'


export default class Categories extends Component {
    render() {
        return (
            <>
                <div className="row row-cols-1 row-cols-md-4 g-4" >
                    <div className="col">
                        <div className="card h-100" style={{borderRadius:20}}>
                            <img src={image1} className="card-img-top" alt="..." style={{ height: '100%', width: '100%',borderRadius:20}} />
                            <div class="card-img-overlay" style={{textAlign:'center'}}>
                            <a href="#" class="btn btn-danger" style={{marginTop:'50%'}}>Mobile</a>
                            </div>
                        </div>
                    </div>
                </div>
                <div style={{marginTop:10,textAlign:'right'}}>
                    <Link to='./posts'><a class="btn btn-dark" type="button">View All</a></Link>
                </div>
                    
                
            </>
        )
    }
}
