import React, { Component } from 'react'
import {Link} from 'react-router-dom'


export default class AdminHeader extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark fixed-top bg-dark">
                    <div className="container-fluid">
                        <Link className="navbar-brand" to="/">Admin Panel</Link>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <Link className="nav-link active" aria-current="page" to="/manageposts">ManagePosts</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/manageprofile">ManageProfile</Link>
                                </li>
                                </ul>
                        
                            
                            <Link to="/adminlogin"><button className="btn btn-outline-danger" type ="submit" style={{marginLeft:10}}>Login</button></Link>
                    
                    </div>
                </nav>
            </div>
        )
    }
}
