import React, { Component } from 'react'

export default class Post extends Component {
    
    render() {
        let { title, description, byAuthor, imgUrl, category } = this.props;
        console.log(this.props);
        const onClickHandler = pos => {
            
            pos.onClckData(this.props);

        }
        return (
            <div>
                <div className="card h-100">
                    <img src={imgUrl} className="card-img-top" alt="..." />
                    <div class="card-body" >
                        <h5 class="card-title">{title}</h5>
                        <p class="card-text">{description}</p>
                        <p class="card-text">By : {byAuthor}</p>
                        <p class="card-text">Category : {category}</p>
                        <button class="btn btn-outline-danger btn-sm" onClick={onClickHandler}>Read More</button>
                    </div>

                </div>

            </div>
        )
    }
}
