import React, { Component } from "react";
import Categories from "./Categories";
import LatestBlog from "./LatestBlog";
import Slide from "./Slide";

export const postData = [
  {
      title : "my post",
      desc : "hello this is my post"
  },
  {
      title : " post",
      desc : " this is my post"
  },
  {
    title : " post",
    desc : " this is my post"
},
{
  title : " post",
  desc : " this is my post"
},
{
  title : " post",
  desc : " this is my post"
}
]

export default class ShowBlog extends Component {
  render() {
    return (
      <div>
        {/* Slider */} 
        <Slide />

        {/* Latest Blog */}
        <div className="container" style={{ marginTop: 40 }}>
          <h1
            style={{
              textAlign: "center",
              borderBottom: "2px solid black",
              paddingBottom: 4,
            }}
          >
            Latest Posts
          </h1>
          {/*data from json*/}
          <div className="row my-3">
          {postData.map((element)=>{
            return <div className="col-md-3" style={{ marginTop: 30 }}>
            <LatestBlog title={element.title} desc={element.desc}/>
          </div>
          })}
          </div>
        </div>
        <div className="container" style={{ marginTop: 40 }}>
          <h1>Categories</h1>
          <div style={{ marginTop: 30 }}>
            <Categories/>
          </div>
        </div>
      </div>
    );
  }
}
