import React, { Component } from 'react'
import Post from './Post';
import Spinner from './Spinner';

export default class PostView extends Component {

    constructor() {
        super();
        console.log("hello this is component");

        this.state = {
            posts: [],
            loading: false,
        }
    }
    

    async componentDidMount() {
        let url = `http://localhost:8080/MyBlog-1.0/ViewPost`;
        this.setState({loading: true});
        let data = await fetch(url);
        let parsedata = await data.json();    
        this.setState({ posts: parsedata, 
            loading:false})
            console.log(parsedata);
    }
    render() {

        const onClickSaveDateHadnler = (props) => {
            const postData = {
                ...props
            };
            props.data(postData);
        }
        return (
            <div className="container my-4">
                <h1 style={{textAlign:'center', marginTop:'8%'}}>All Posts</h1>
                {this.state.loading && <Spinner />}
                <div className="row row-cols-1 row-cols-md-4 g-4 my-4">
                {!this.state.loading && this.state.posts.map((element) => {
                  return  <div className="col">
                      <Post onClickDate={onClickSaveDateHadnler} title={element.title} description={element.description.slice(0, 80)} byAuthor={element.byAuthor} category={element.category} imgUrl={element.imgUrl}/>
                    </div>
                })}
                </div>
                <div className="container d-flex justify-content-between">
                    <button type="button"  className="btn btn-dark">&larr; Previous</button>
                    <button type="button"  className="btn btn-dark">Next &rarr;</button>
                </div>
            </div>
        )
    }
}
