import React, { Component } from 'react'
import {Link} from 'react-router-dom'


export default class Header extends Component {
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark fixed-top bg-dark">
                    <div className="container-fluid">
                        <Link className="navbar-brand" to="/">My-Blog</Link>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                                <li className="nav-item">
                                    <Link className="nav-link active" aria-current="page" to="/">Home</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/postview">Posts</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/about">About</Link>
                                </li>
                                </ul>
                               
                            <form className="d-flex">
                                <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
                                <button className="btn btn-outline-danger" type ="submit">Search</button>
                            </form>
                            <Link to="/adminlogin"><button className="btn btn-outline-danger" type ="submit" style={{marginLeft:10}}>Login</button></Link>
                            <Link to="/adminpanel"><button className="btn btn-outline-danger" type ="submit" style={{marginLeft:10}}>Sigin</button></Link>
                        </div>
                    </div>
                </nav>
            </div>
        )
    }
}
