import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class AddPost extends Component {
    render() {
        return (
            <div className="container" style={{ marginTop: 80 }}>
                <Link to="/"><button type="submit" class="btn btn-primary">View Posts</button></Link>
                <Link to="/addpost"><button type="submit" class="btn btn-primary" style={{ marginLeft: 20 }}>Add Post</button></Link>
                <div className="container my-4" style={{width:'50%'}}>
                    <h1 style={{ textAlign: 'center' }}>Add Post</h1>
                    <form>
                        <div class="mb-3">
                            <label for="exampleInputTittle" class="form-label">Tittle</label>
                            <input type="text" class="form-control" id="exampleInputTitte"/>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}
