import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import image1 from '../images/img1.jpg'



export default class LatestBlog extends Component { 
    render() {
        let {title, desc} = this.props
        return (
            <div >
            <div className="card h-100 my-3">
                <img src={image1} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">{title}</h5>
                    <p className="card-text text-danger">{desc}</p>
                    <a target="_blank" rel="noreferrer" className="btn btn-sm btn-dark">Read More</a>
                </div>
            </div>
            </div>
            
        )
    }
}
