import React, { Component } from 'react'
import {Link} from 'react-router-dom'

export default class ManagePosts extends Component {
    render() {
        return (
            <div className="container" style={{marginTop:80}}>
                <Link to="/"><button type="submit" class="btn btn-primary">View Posts</button></Link>
                <Link to="/addpost"><button type="submit" class="btn btn-primary" style={{marginLeft:20}}>Add Post</button></Link>
                
                <div className="container my-4" >
                    <h1>View Posts</h1>
                    <table style={{width:'100%', marginTop:20}}>
                    <tr style={{textAlign:'left',padding:8}}>
                        <th>S.No</th>
                        <th>Tittle</th>
                        <th>Edit</th>
                        <th>Delete</th>
                        <th>Publish</th>
                    </tr>
                    <tr>
                        <td>1</td>
                        <td>Science</td>
                        <td style={{color:'blue'}}>edit</td>
                        <td style={{color:'red'}}>delete</td>
                        <td style={{color:'green'}}>publish</td>
                    </tr>
                    </table>
                </div>
                
            </div>
        )
    }
}
